  animation-fill-mode: both;
  animation-name: fade-in; }

.fade-in-slow {
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-name: fade-in; }

.fade-out {
  animation-duration: 0.1s;
  animation-fill-mode: both;
  animation-name: fade-out; }

.fade-out-slow {
  animation-duration: 0.3s;
  animation-fill-mode: both;
  animation-name: fade-out; }

.slide-panel--xs {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s ease-out; }
  .slide-panel--xs.is-open {
    max-height: 200px;
    overflow: hidden;
    transition: max-height 0.5s ease-in; }

.slide-panel--sm {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s ease-out; }
  .slide-panel--sm.is-open {
    max-height: 400px;
    overflow: hidden;
    transition: max-height 0.5s ease-in; }

.slide-panel--md {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s ease-out; }
  .slide-panel--md.is-open {
    max-height: 1000px;
    overflow: hidden;
    transition: max-height 1s ease-in; }

.slide-panel--xl {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s ease-out; }
  .slide-panel--xl.is-open {
    max-height: 3000px;
    overflow: hidden;
    transition: max-height 1s ease-in; }

.toggle {
  display: none; }
  .toggle.is-active {
    display: block; }

.toggle-fade {
  display: none; }
  .toggle-fade.is-active {
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-name: fade-in;
    display: block; }

.toggle-slide {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s ease-out; }
  .toggle-slide.is-active {
    max-height: 1000px;
    overflow: hidden;
    transition: max-height 1s ease-in; }
  .toggle-slide--xs.is-active {
    max-height: 200px;
    overflow: hidden;
    transition: max-height 0.8s ease-in; }
  .toggle-slide--sm.is-active {
    max-height: 400px;
    overflow: hidden;
    transition: max-height 0.8s ease-in; }
  .toggle-slide--md.is-active {
    max-height: 1000px;
    overflow: hidden;
    transition: max-height 1s ease-in; }
  .toggle-slide--xl.is-active {
    max-height: 3000px;
    overflow: hidden;
    transition: max-height 1s ease-in; }
